import React from 'react';

import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMinus, faPlus, faTimes } from '@fortawesome/free-solid-svg-icons';

import './row.css';

library.add(faMinus, faPlus, faTimes);

function Row(props) {
    const { item, onAdd,onRemove,onX} = props
    return (
        <tr>
            <td className=" position-relative">
                <img src={item.imgUrl}
                    alt="..." width="95" height="115" />

                <p className=" fs-5 text">{item.name}</p>

            </td>
            <td className="position-relative">

                <p className=" fs-5 price">${item.price}</p>

            </td>

            <td className="position-relative">

                <p className=" fs-5 piece">
                    <FontAwesomeIcon icon="minus" size="0.75x" className="fas fa-minus fa-sm mx-3 ops"
                        onClick={() => onRemove(item)} aria-disabled="true" />
                    {item.qty}
                    <FontAwesomeIcon icon="plus" size="0.75x" className="fas fa-plus fa-sm mx-3 ops"
                        onClick={() => onAdd(item)} aria-disabled="true" />
                </p>

            </td>

            <td className="position-relative">

                <p className=" fs-5 total">
                    ${(item.qty * item.price).toFixed(2)}
                </p>

            </td>
            <td className="position-relative">
                <span className=" fs-5 x">
                    <FontAwesomeIcon icon="times" size="0.75x" className="fas fa-times fa-sm ops"
                    onClick={() => onX(item)} />
                </span>
            </td>
        </tr>
    )
}

export default Row;