import React from 'react';

import { NavLink } from 'react-router-dom';
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faShoppingCart } from '@fortawesome/free-solid-svg-icons';

import './navbar.css';

library.add(faShoppingCart);

function Navbar(props) {
    const { quantity } = props;
    return (
        <>
            <nav className="navbar fixed-top navbar-dark bg-dark">
                <div className="container-fluid">
                    <NavLink to="/" className="navbar-brand">Shopping</NavLink>

                    <ul className="nav justify-content-end" style={{ fontSize: "0.5rem" }}>
                        <li className="nav-item">


                            <NavLink to="/my-cart">
                                <FontAwesomeIcon icon="shopping-cart" size="3x"
                                    className="fas fa-shopping-cart fa-3x i" aria-disabled="true" /> </NavLink>
                                <span className="quantity">{quantity}</span>
                           



                        </li>

                    </ul>
                </div>

            </nav>

        </>
    )
}

export default Navbar;