import React from 'react';

import './cart.css';
import Row from './Row/Row';

export default function Cart(props) {
    const { cartItems,onAdd,onRemove,onX } = props;
    var sum = cartItems.reduce( function(total, item){
        return total + item.price*item.qty
      },0);
    return (
        <>
            <div className="container justify-content-end">
                <h1 className="text-center mt-5">My Cart</h1>
                {cartItems.length === 0 && <div>Cart is Empty</div>}
                {cartItems.length !== 0 &&
                    <table className="table table-borderless mt-2">
                        <thead>
                            <tr>
                                <th scope="col" className=" text-center">Product</th>
                                <th scope="col" className=" text-center">Price</th>
                                <th scope="col" className=" text-center">Piece</th>
                                <th scope="col" className=" text-center">Total Price</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {cartItems.map((item)=>(
                                <Row onAdd={onAdd} onRemove={onRemove} key={item.id} item={item} onX={onX}/>
                            ))}


                        </tbody>
                    </table>

                }


                <div id="check" className="position-relative">
                    <h2 id="check-price">${sum.toFixed(2)}</h2>
                    <button className="btn btn-outline-dark btn-lg" onClick={()=>alert(`The total cost of the products in your cart is $${sum.toFixed(2)}`)} id="check-btn">Check Out</button>
                </div>

            </div>
        </>
    )
}
