import React from 'react';

import { Route, Routes } from 'react-router-dom';
import { useState } from 'react';

import MainCard from './components/MainCard/MainCard';
import Navbar from './components/Navbar/Navbar';
import { data } from './products';
import Cart from './components/Cart/Cart';

function App() {
  const { products } = data;
  const [cartItems, setCartItems] = useState([]);
  const onAdd = (product) => {
    const exist = cartItems.find((x) => x.id === product.id);
    if (exist) {
      setCartItems(
        cartItems.map((x) =>
          x.id === product.id ? { ...exist, qty: exist.qty + 1 } : x)
      )
    } else {
      setCartItems([...cartItems, { ...product, qty: 1 }])
    }
  }
  const onRemove = (product) => {
    const exist = cartItems.find((x) => x.id === product.id);
    if (exist.qty === 1) {
      setCartItems(
        cartItems.filter((x) => x.id !== product.id)
      )
    } else {
      setCartItems(
        cartItems.map((x) =>
          x.id === product.id ? { ...exist, qty: exist.qty - 1 } : x)
      )
    }
  }
  const onX = (product) => {
    setCartItems(
      cartItems.filter((x) => x.id !== product.id)
    )
  }
  return (

    <>
      <Navbar quantity={cartItems.reduce((total,item)=>total+item.qty,0)} />
      <Routes>
        <Route path="/" exact element={<MainCard onAdd={onAdd} products={products} />} />
        <Route path="/my-cart" exact element={ <Cart onAdd={onAdd} onRemove={onRemove} cartItems={cartItems} onX={onX} />} />
      </Routes>
    </>

  );
}

export default App;
