import React from 'react';

import './card.css';

export default function Card(props) {
    const {product,onAdd}=props;
    return (
        <div className="card col col-md-2 mt-3 mx-4"  onClick={() => onAdd(product)}>
            <img src={product.imgUrl}
                className="card-img-top" alt="..."/>
            <div className="card-body">
                <p className="card-text text-center fs-6 position-relative">
                    {product.name}</p>
                <p className="card-text text-center fw-bolder fs-4">${product.price}</p>
                <button className="btn btn-outline-dark p-2 w-100">Add to cart</button>
            </div>
        </div>
    )
}

